<template>
  <div>
    <b-row class="justify-content-between px-6 mb-3">
      <b-col
        class="font-weight-bold justify-content-center mobileView mt-2"
        cols="12"
        xl="9"
        >{{ evaluateYear + $t("EVALUATEYEARHEAD") }}</b-col
      >
      <b-col
        class="d-flex justify-content-end float-right prevNextMobileView"
        cols="12"
        xl="3"
      >
        <div
          class="py-2 px-2 pillButtons bg-white d-flex justify-content-center align-items-center"
        >
          <b-link
            :disabled="prevLinkDisabled"
            @click="onPreviousClick"
            href="javascript:;"
            class="link d-flex justify-content-center align-items-center"
          >
            <span class="">
              <i
                class="fa fa-caret-left fa-2x text-muted mr-2"
                aria-hidden="true"
              ></i>
            </span>
            <span class="mr-2">{{ $t("PREVIOUSYEAR") }}</span>
          </b-link>
          &nbsp;|
          <b-link
            :disabled="nextLinkDisabled"
            @click="onNextClick"
            href="javascript:;"
            class="link ml-2 d-flex justify-content-center align-items-center"
          >
            <span>{{ $t("FOLLOWINGYEAR") }}</span>
            <span class="">
              <i
                class="fa fa-caret-right fa-2x text-muted ml-2"
                aria-hidden="true"
              ></i>
            </span>
          </b-link>
        </div>
      </b-col>
    </b-row>
    <b-row>
      <b-col id="expenseEvaluationTable" xl="12">
        <l-table
          :hasPagination="false"
          :showPerPage="false"
          :perPage="limit"
          :isBusy="isBusy"
          :table-data="expenseEvaluationTableData"
          :fields="fields"
          :total="total"
          :striped="true"
          :startPage="start"
          :serverSideProcess="true"
          @onSeeMore="toSee"
          @onExpenseDetails="expenseDetails"
          :sort-by-desc="sortByDesc"
          :outlined="false"
          head-variant="dark"
          tr-class="rowHeight"
          small="small"
        ></l-table>
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="12" class="float-right">
        <div class="d-flex align-items-center float-right pointer">
          {{ lastUpdatedDate }}
        </div>
      </b-col>
    </b-row>
  </div>
</template>
<script>
import LTable from "@/components/ListTable";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "ExpenseEvaluationList",
  components: { LTable },
  data() {
    return {
      sortByDesc: true,
      editObject: {},
      testModalShow: false,
      testBusy: false,
      isBusy: false,
      total: 0,
      limit: 10,
      start: 0,
      expenseEvaluationTableData: [],
      evaluateYear: "",
      getApiYear: "",
      nextLinkDisabled: true,
      prevLinkDisabled: false,
      scalingIcon: ["fa-sun-o", "fa-cloud", "fa-umbrella"],
      overAllYearJudgement: [
        this.$t("OVERALLJUDGEMENTCHART.SLIM"),
        this.$t("OVERALLJUDGEMENTCHART.HEALTHY"),
        this.$t("OVERALLJUDGEMENTCHART.FAT"),
        this.$t("OVERALLJUDGEMENTCHART.METABO"),
        this.$t("OVERALLJUDGEMENTCHART.SPOILER"),
      ],
      lastUpdatedDate: "",
      treeData: [],
    };
  },
  computed: {
    fields() {
      return [
        {
          key: "account",
          label: this.$t("ACCOUNT"),
          class: "text-center normal-width",
        },
        {
          key: "expense_item",
          label: this.$t("EXPENSEITEM"),
          class: "text-center normal-width",
        },
        {
          key: "monthly_average",
          label: this.$t("MONTHLYAVERAGE"),
          class: "text-center normal-width",
        },
        {
          key: "annual_total_amount",
          label: this.$t("ANNUALTOTALAMOUNT"),
          class: "text-center  normal-width",
        },
        {
          key: "last_year_average",
          label: this.$t("LASTYEARAVERAGE"),
          class: "text-center normal-width",
        },
        {
          key: "industry_average_amount",
          label: this.$t("INDUSTRYAVERAGEAMOUNT"),
          class: "text-center normal-width ",
        },
        {
          key: "same_scale_evaluation_sales",
          label: this.$t("SAMESCALEEVALUATION"),
          sublabel: this.$t("SALES"),
          class: "text-center normal-width",
        },
        {
          key: "same_scale_evaluation_employee",
          label: this.$t("SAMESCALEEVALUATION"),
          sublabel: this.$t("NOOFEMPLOYEE"),
          class: "text-center normal-width",
        },
        {
          key: "same_scale_evaluation_tsubo",
          label: this.$t("SAMESCALEEVALUATION"),
          sublabel: this.$t("SUBO"),
          class: "text-center normal-width",
        },
        {
          key: "overall_evaluation_year",
          label: this.$t("OVERALLEVALUATIONYEAR"),
          sublabel: this.$t("YEARPARENTHESIS"),
          class: "text-center normal-width",
        },
        {
          key: "amount_reduction_year",
          label: this.$t("AMOUNTOFREDUCTIONYEAR"),
          sublabel: this.$t("YEARPARENTHESIS"),
          class: "text-center normal-width",
        },
        {
          key: "reduction_plan",
          label: this.$t("REDUCTIONPLAN"),
          class: "text-center normal-width",
        },
      ];
    },
  },
  mounted() {
    this.$store.dispatch("updateExpenseItemYear", {
      expense_item_year: "",
    });
    this.getExpenseEvaluateData();
  },
  created() {},
  methods: {
    /**
     * @description function to get selected expenseItem of reduction proposal
     * @description updating to store and route to reduction_proposal_preview screen
     * @param {object} updateSelectedExpenseItem
     */
    toSee(item) {
      this.$store.dispatch("updateSelectedExpenseItem", {
        accounts_name_cd: item.item.accounts_code,
        expense_item_cd: item.item.expenseItemCode,
        expense_item_nm: item.item.expense_item,
      });
      this.$router.push({ name: "reduction_proposal_preview" });
    },
    /**
     * @description function to get selected expenseItem of reduction proposal
     * @description updating to store and route to monthly_expense_details screen
     * @param {object} updateSelectedExpenseItem
     */
    expenseDetails(item) {
      this.$store.dispatch("updateSelectedExpenseItem", {
        accounts_name_cd: item.item.accounts_code,
        expense_item_cd: item.item.expenseItemCode,
        expense_item_nm: item.item.expense_item,
      });
      this.$store.dispatch("updateExpenseItemYear", {
        expense_item_year: this.evaluateYear,
      });
      this.$router.push({ name: "monthly_expense_details" });
    },
    /**
     * @description function to convert number format to japanese currency format
     * @param {any} int
     * @param {any} value
     * @returns string currency value
     */
    filterByCurrency(value) {
      try {
        return parseInt(value)
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      } catch {
        return 0;
      }
    },
    /**
     * @description function to fetch available reduction proposal expense Item and compile it as table format
     * @param {int} client_no
     * @param {string} year
     * @returns {any} expenseEvaluationTableData
     */
    async getExpenseEvaluateData() {
      this.isBusy = true;
      this.prevLinkDisabled = true;
      this.nextLinkDisabled = true;
      const { data } = await BasicInfoApis.postAPI(endPoint.EVALUATIONLIST, {
        client_no: localStorage.getItem("client_account_no"),
        year: this.evaluateYear,
      });
      this.isBusy = false;
      let responseData = data.data.clientJudgInfoYListObject;
      if (this.evaluateYear == "") {
        this.getApiYear = data.data.clientAvailableYear;
      }

      if (responseData.length <= 0) {
        this.prevLinkDisabled = true;
        this.nextLinkDisabled = true;
        if (this.evaluateYear == "") {
          this.evaluateYear = data.data.clientAvailableYear;
        } else {
          this.evaluateYear = (parseInt(this.evaluateYear) + 1).toString();
          this.getExpenseEvaluateData();
        }
      } else {
        this.prevLinkDisabled = false;
        this.nextLinkDisabled = false;
        this.evaluateYear = data.data.clientAvailableYear;
        this.lastUpdatedDate =
          data.data.clientOtherInfoObject.update_dt.slice(0, 4) +
          this.$t("YEAR") +
          data.data.clientOtherInfoObject.update_dt.slice(5, 7) +
          this.$t("MONTH") +
          data.data.clientOtherInfoObject.update_dt.slice(8, 10) +
          this.$t("SUNDAY") +
          this.$t("UPDATE");
        let mapData = [];
        let type = 1;
        let proposalType;
        let sortedData = this._.sortBy(responseData, "accounts_name_cd");
        /**
         * @description grouping list of expenseItems data which have same accounts
         * @param {string} accounts_name_cd
         */
        sortedData.forEach((item) => {
          if (mapData.length > 0) {
            if (
              mapData[mapData.length - 1].accounts_code == item.accounts_name_cd
            ) {
              proposalType += 0; /**proposal Type will be same when same accounts encountered during iteration of expenseItem */
            } else {
              proposalType += 1; /**proposal Type will get incremented when different accounts encountered during iteration of expenseItem */
            }
          } else {
            proposalType = type;
          }
          let listData = {
            type: proposalType ? proposalType : "-",
            accounts_code: item.accounts_name_cd ? item.accounts_name_cd : "-",
            account: item.accounts_name_nm ? item.accounts_name_nm : "-",
            expense_item: item.expense_item_nm ? item.expense_item_nm : "-",
            expenseItemCode: item.expense_item_cd ? item.expense_item_cd : "-",
            monthly_average: item.cost_monthly_average
              ? this.$t("GLOBALYEN") +
                this.filterByCurrency(item.cost_monthly_average)
              : "-",
            annual_total_amount: item.cost_year_total
              ? this.$t("GLOBALYEN") +
                this.filterByCurrency(item.cost_year_total)
              : "-",
            last_year_average: item.cost_lastyear_average
              ? this.$t("GLOBALYEN") +
                this.filterByCurrency(item.cost_lastyear_average)
              : "-",
            industry_average_amount: item.cost_industry_average
              ? this.$t("GLOBALYEN") +
                this.filterByCurrency(item.cost_industry_average)
              : "-",
            same_scale_evaluation_sales: item.judg_same_size_revenue
              ? this.scalingIcon[parseInt(item.judg_same_size_revenue) - 1]
              : "-",
            same_scale_evaluation_employee: item.judg_same_size_employee_number
              ? this.scalingIcon[
                  parseInt(item.judg_same_size_employee_number) - 1
                ]
              : "-",
            same_scale_evaluation_tsubo: item.judg_same_size_floor_space
              ? this.scalingIcon[parseInt(item.judg_same_size_floor_space) - 1]
              : "-",
            overall_evaluation_year: item.judg
              ? this.overAllYearJudgement[parseInt(item.judg) - 1]
              : "-",
            amount_reduction_year: item.cost_reduction_range
              ? this.$t("GLOBALYEN") +
                this.filterByCurrency(item.cost_reduction_range)
              : "-",
            reduction_plan: item.proposal_exist,
          };
          mapData.push(listData);
        });
        this.expenseEvaluationTableData = mapData;

        this.expenseEvaluationTableData.forEach((element, index) => {
          if (element.type > type) {
            type = element.type;
            this.expenseEvaluationTableData[index - 1].last_element = true;
          }
        });
        if (this.expenseEvaluationTableData.length > 0) {
          this.expenseEvaluationTableData[
            this.expenseEvaluationTableData.length - 1
          ].last_element = true;
        }
      }
    },
    /**
     * @description function to fetch next year expense Evaluation List if available
     */
    onNextClick() {
      this.prevLinkDisabled = false;
      if (parseInt(this.getApiYear) < parseInt(this.evaluateYear) + 1) {
        this.nextLinkDisabled = true;
      } else {
        this.evaluateYear = (parseInt(this.evaluateYear) + 1).toString();
        this.getExpenseEvaluateData();
      }
    },
    /**
     * @description function to fetch previous year expense Evaluation List if available
     */
    onPreviousClick() {
      this.nextLinkDisabled = false;
      this.evaluateYear = (parseInt(this.evaluateYear) - 1).toString();
      this.getExpenseEvaluateData();
    },
    rowClass(item, type) {
      if (!item || type !== "row") return;
      if (item.last_element) return "t-bottom-border";
    },
  },
};
</script>
<style scoped lang="scss">
$primary: #062854 !default;
.selected {
  color: $primary !important;
}
.card-link + .card-link {
  margin-left: 0rem;
}

.graph-area {
  min-height: auto;
}
.tree-text,
.link-text {
  font-size: 0.7rem;
  font-weight: 800;
  color: $primary;
}
hr.thick {
  border-top: 2px solid $primary;
}
.txt-line-height {
  line-height: 2.1;
}
.title-text {
  font-size: 20px;
}
.pillButtons {
  box-shadow: 0px 0px 30px #0000001a;
  border-radius: 16px;
  width: 10rem;
  height: 31px !important;
  font-weight: 500 !important;
}
.graph-selected,
.graph-selected:hover {
  color: rgb(109, 86, 240);
}
.w-25 {
  min-width: 1rem !important;
}
.rowHeight {
  height: 50px !important;
}
@media (max-width: 991px) {
  .mobileView {
    text-align: center !important;
    font-size: 16px !important;
    margin-bottom: 1rem !important;
  }
  .prevNextMobileView {
    justify-content: center !important;
  }
}
@media only screen and (min-width: 991px) {
  #expenseEvaluationTable ::-webkit-scrollbar {
    width: 3px;
    height: 3px;
    background: #d3d3d3 0% 0% no-repeat padding-box;
    border-radius: 4px;
    opacity: 1;
  }
  #expenseEvaluationTable ::-webkit-scrollbar-thumb {
    background: #033170;
    height: 3px !important;
    -webkit-border-radius: 1ex;
    -webkit-box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.75);
  }
  #expenseEvaluationTable ::-webkit-scrollbar-corner {
    background: #033170 0% 0% no-repeat padding-box !important;
  }
}
</style>
<style lang="scss">
.normal-width {
  width: 80px !important;
  vertical-align: inherit !important;
}
.t-bottom-border {
  border-bottom: 1px dashed gray;
}
.rowHeight {
  height: 50px !important;
}
#expenseEvaluationTable * {
  scrollbar-width: thin;
  scrollbar-color: #033170 #cfd8dc;
}
@media only screen and (min-width: 991px) {
  #expenseEvaluationTable ::-webkit-scrollbar {
    width: 3px;
    height: 3px;
    background: #d3d3d3 0% 0% no-repeat padding-box;
    border-radius: 4px;
    opacity: 1;
  }
  #expenseEvaluationTable ::-webkit-scrollbar-thumb {
    background: #033170;
    height: 3px !important;
    -webkit-border-radius: 1ex;
    -webkit-box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.75);
  }
  #expenseEvaluationTable ::-webkit-scrollbar-corner {
    background: #033170 0% 0% no-repeat padding-box !important;
  }
}
</style>